@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
/* Fonts */

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #eff2f4;
  font-family: 'Raleway', sans-serif;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap {
  flex: 1 1;
}

/* Color Choices */

:root {
  --primary-color: #3f6ad8;
  --dark-color: #2e2e2e;
  --accent-color: #ebebeb;
  --grey-color: #fafbfc;
  --border-color: #e9ecef;
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #6770d2;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
}

/* Globals */

.large {
  font-size: 25px;
}

.small {
  font-size: 14px;
}
.header {
  height: 50px;
  margin: 0 0 10px 0;
  border-bottom: 1px solid #e9ecef;
  border-bottom: 1px solid var(--border-color);
}

.header-text {
  padding: 10px;
}

.header-icon {
  opacity: 10%;
  font-size: 20px;
}

.bold {
  font-weight: bold;
}

.blue {
  color: #3f6ad8;
  color: var(--primary-color);
  font-size: 20px;
}

.white {
  color: #ffffff;
}

.green {
  color: #6ad29d;
}

.shadow {
  box-shadow: -1px -10px 37px -4px rgba(0, 0, 0, 0.48);
}

.card-shadow {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.11), 0 6px 6px rgba(0, 0, 0, 0.1);
}

.flex {
  display: flex;
}

.align-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-around {
  justify-content: space-around;
}

.justify-even {
  justify-content: space-evenly;
}

.justify-center {
  justify-content: center;
}
.justify-left {
  justify-content: flex-start;
}
.justify-right {
  justify-content: flex-end;
}

.flex-column {
  flex-direction: column;
}

/* Margin */
.nav-margin {
  margin-top: 200px;
}

.my {
  margin-right: 5px;
}

/* Padding */

.p {
  padding: 20px;
}

/* Navbar */
.logo {
  padding: 0 15px 0 15px;
  height: 100%;
  font-size: 19px;
}

.logo-light {
  color: #fff;
  color: var(--white);
  padding: 0 15px 0 15px;
  height: 100%;
  font-size: 19px;
}

.logo-icon {
  margin: 0 0 5px 2px;
}
.nav-bar {
  padding: 0 10px 0 10px;
  background-color: #fafbfc;
  background-color: var(--grey-color);
  width: 100%;
  height: 60px;
  left: 0;
  position: fixed;
  z-index: 2;
}
.side-nav {
  background-color: #fff;
  background-color: var(--white);
  padding: 65px 5px 5px 5px;
  height: 100vh;
  width: 250px;
  position: fixed;
  left: 0;
  background: #fff;
  background: var(--white);
  border: 1px solid #e9ecef;
  border: 1px solid var(--border-color);
  z-index: 1;
}

/* Footer */
.footer {
  background: #343a40;
  background: var(--gray-dark);
  position: relative;
  bottom: 0;
  height: 50px;
  width: 100%;
  padding: 10px;
  color: #fff;
  color: var(--white);
  font-size: 14px;
}

.footer a {
  color: #fff;
  color: var(--white);
  text-decoration: none;
}

.footer a:hover {
  color: #6c757d;
  color: var(--gray);
}
/* Loader */

.loader {
  font-size: 70px;
  color: #3f6ad8;
  color: var(--primary-color);
  background: #fff;
  background: var(--white);
  height: 100vh;
  width: 100%;
}

.spinner {
  position: absolute;
  height: 100px;
  width: 100px;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
  background-size: 100%;
}

.numbers {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  margin: 100px 0 10px 0px;
  color: var(--white);
}

.number-item {
  border-radius: 5px;
  border: 1px solid var(--border-color);
  padding: 20px;
  background: var(--white);
  color: var(--dark);
  margin: 0 0 0 15px;
  width: 250px;
}

.date-item {
  border-radius: 5px;
  padding: 27px;
  background: url(/Dashboard-Firebase/static/media/numbers-background.701d76c0.jpg);
  width: 50%;
  margin: 0 0 0 15px;
}

.number-item:first-child {
  margin: 0;
}

.box {
  height: 60px;
  width: 75px;
  padding: 10px;
  border-radius: 5px;
  font-size: 30px;
  margin: 0 10px 0 0;
  color: var(--white);
}

.records {
  background: var(--orange);
}

.records-ready {
  background: var(--green);
}

.alert {
  background: var(--red);
}

/* Funds List */
.funds-container {
  margin: 20px 0 0 0;
  padding: 20px;
  background: #fff;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  border: 1px solid var(--border-color);
}

.funds-table {
  margin: 0;
  padding: 0;
  border-collapse: collapse;
  border: 1px solid var(--border-color);
  width: 100%;
  font-size: 12px;
}

.funds-table th {
  font-size: 13px;
  padding: 10px 10px 10px 5px;
  text-align: left;
  border: 1px solid var(--border-color);
}

.funds-table td {
  padding: 8px 0 8px 10px;
  border: 1px solid var(--border-color);
}

.funds-table tr:nth-of-type(even) {
  background-color: #f0f0f0;
}

.funds-table tr:hover {
  background-color: #e0f3ff;
}

.top-pagination {
  margin: 20px 0 0 0;
}

.pagination {
  padding: 10px;
  font-size: 13px;
}

.pagination button {
  font-weight: bold;
  cursor: pointer;
  border: 1px solid var(--border-color);
  background: none;
  padding: 5px;
  color: var(--primary-color);
  font-size: 12px;
}

.pagination button:hover {
  background: var(--accent-color);
}

.pagination button:nth-child(4) {
  margin: 0 10px 0 0;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}
.pagination button:nth-child(1) {
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

.page-input input {
  border: 1px solid var(--border-color);
  width: 30px;
  height: 30px;
  margin: 0 10px 0 0;
  padding: 10px;
  border-radius: 5px;
}

.page-input {
  font-size: 13px;
  margin: 0 0 10px 10px;
  font-size: 12px;
}

.top-pagination select {
  border: 1px solid var(--border-color);
  width: 50px;
  height: 30px;
  border-radius: 5px;
}

.entries-input {
  margin: 0 0 10px 0;
  font-size: 12px;
}

.pageinfo-container {
  padding: 10px;
}

.page {
  font-size: 13px;
}

.search-input {
  border: 1px solid var(--border-color);
  height: 30px;
  margin: 0 0 10px 0;
  padding: 10px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.search-icon {
  border: 1px solid var(--border-color);
  margin: 0 0 10px 0;
  height: 30px;
  width: 30px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  color: rgb(214, 214, 214);
}

